:root {
  margin: 0;
  padding: 0;
}

.people {
  grid-template-columns: repeat(2, 200px);
  place-content: center;
  gap: 12px 40px;
  width: 100%;
  padding-top: 8px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.person {
  flex-direction: column;
  gap: 8px;
  width: 200px;
  padding-bottom: 16px;
  transition: transform 1s;
  display: flex;
  position: relative;
  overflow: visible;
}

.person:hover {
  z-index: 1;
  background-color: #eee;
  border-radius: 10px;
  transform: scale(1.2);
}

.person:hover .extra {
  opacity: 1;
  display: block;
}

.person:hover .photo img {
  border-radius: 25%;
}

.photo {
  width: 200px;
  height: 200px;
  position: relative;
}

.photo img {
  object-fit: cover;
  border-radius: 100%;
  width: 100%;
  height: 100%;
  transition: border-radius 1s;
  display: block;
}

.rank {
  text-align: center;
  background-color: #fff;
  border-radius: 100%;
  width: 32px;
  line-height: 32px;
  display: block;
  position: absolute;
  top: 50%;
  left: 85%;
  transform: translate(-50%, -50%);
}

.details {
  flex-direction: column;
  align-items: center;
  gap: 8px;
  display: flex;
}

.name {
  letter-spacing: 0%;
  text-align: center;
  font-family: Arial;
  font-size: 20px;
  font-weight: 700;
  line-height: 100%;
}

.extra {
  letter-spacing: 0%;
  text-align: center;
  opacity: 0;
  font-family: Arial;
  font-size: 18px;
  font-weight: 400;
  line-height: 100%;
}

.sport:after {
  content: ",";
}

.pay:before {
  content: "$";
}

.pay:after {
  content: "M";
}
